.navbar {
  background: #3d3d3d;
  display: flex;
}

.navbar img {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 2px 10px;
}

.navbar .logo_name {
  font-size: 25px;
  color: #ececec;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}
