.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #dad2c3;
  border-radius: 10px;
  position: absolute;
  top: 25%;
  right: 50%;
  transform: translate(50%, 50%);
  padding: 20px 50px;
}

.auth .text {
  font-size: 18px;
  text-align: center;
  margin-bottom: 25px;
}

.auth button {
  border: none;
  background-color: #55bf40;
  color: white;
  border-radius: 8px;
  padding: 15px 20px;
  white-space: nowrap;
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.3);
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
}

.auth button:hover {
  opacity: 0.9;
  transition: all 0.4s ease;
}
