body {
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  display: flex;
  background: #dad2c3;
  flex-direction: column;
  border-radius: 10px;
  margin-top: 25px;
}

.room {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 50px;
}

.room label {
  text-align: center;
  font-size: 25px;
  margin-bottom: 20px;
}

.room input {
  width: 200px;
  height: 30px;
  border: 2px solid #55bf40;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
}

.room button {
  width: 210px;
  height: 40px;
  border: none;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
  background-color: #55bf40;
  color: white;
  cursor: pointer;
}

.sign-out {
  display: flex;
  align-items: center;
  justify-content: end;
  margin: 10px;
}

.sign-out button {
  font-size: 16px;
  background: #494949;
  color: #ececec;
  padding: 5px 10px;
  border-radius: 5px;
}
